import React, { useState, useRef, useEffect } from 'react';
import {
  View,
  StyleSheet,
  TouchableOpacity,
  Animated,
  Text,
  ActivityIndicator,
  Platform,
  Dimensions,
  Pressable,
  PanResponder,
} from 'react-native';
import { Video, ResizeMode, AVPlaybackStatus } from 'expo-av';
import { MaterialIcons } from '@expo/vector-icons';

interface CustomVideoPlayerProps {
  uri: string;
  style?: any;
  onPress?: () => void;
  shouldPlay?: boolean;
}

const { width: SCREEN_WIDTH, height: SCREEN_HEIGHT } = Dimensions.get('window');
const CONTROLS_TIMEOUT = 3000;

const CustomVideoPlayer: React.FC<CustomVideoPlayerProps> = ({
  uri,
  style,
  onPress,
  shouldPlay = false,
}) => {
  const [isPlaying, setIsPlaying] = useState(false);
  const [duration, setDuration] = useState(0);
  const [position, setPosition] = useState(0);
  const [isLoading, setIsLoading] = useState(true);
  const [isControlsVisible, setIsControlsVisible] = useState(true);
  const [isSeeking, setIsSeeking] = useState(false);
  const [videoWidth, setVideoWidth] = useState(0);
  const [videoHeight, setVideoHeight] = useState(0);
  
  const videoRef = useRef<Video>(null);
  const controlsTimeout = useRef<NodeJS.Timeout>();
  const controlsFadeAnim = useRef(new Animated.Value(1)).current;
  const playButtonScale = useRef(new Animated.Value(1)).current;
  const progressWidth = useRef(new Animated.Value(0)).current;
  const sliderPosition = useRef(new Animated.Value(0)).current;

  const panResponder = useRef(
    PanResponder.create({
      onStartShouldSetPanResponder: () => true,
      onMoveShouldSetPanResponder: () => true,
      onPanResponderGrant: (evt) => {
        setIsSeeking(true);
      },
      onPanResponderMove: (evt, gestureState) => {
        const progressBarWidth = SCREEN_WIDTH - 32; // padding * 2
        const progress = Math.max(0, Math.min(1, gestureState.moveX / progressBarWidth));
        sliderPosition.setValue(progress * progressBarWidth);
      },
      onPanResponderRelease: async (evt, gestureState) => {
        const progressBarWidth = SCREEN_WIDTH - 32;
        const progress = Math.max(0, Math.min(1, gestureState.moveX / progressBarWidth));
        const newPosition = progress * duration;
        
        if (videoRef.current) {
          await videoRef.current.setPositionAsync(newPosition);
          setPosition(newPosition);
          progressWidth.setValue(progress);
        }
        setIsSeeking(false);
      },
    })
  ).current;

  useEffect(() => {
    if (shouldPlay && !isPlaying) {
      handlePlayPause();
    }
    return () => {
      if (controlsTimeout.current) {
        clearTimeout(controlsTimeout.current);
      }
    };
  }, [shouldPlay]);

  const animatePlayButton = () => {
    Animated.sequence([
      Animated.timing(playButtonScale, {
        toValue: 0.8,
        duration: 100,
        useNativeDriver: true,
      }),
      Animated.spring(playButtonScale, {
        toValue: 1,
        friction: 3,
        tension: 40,
        useNativeDriver: true,
      }),
    ]).start();
  };

  const hideControlsWithDelay = () => {
    if (controlsTimeout.current) {
      clearTimeout(controlsTimeout.current);
    }
    
    if (isPlaying) {
      controlsTimeout.current = setTimeout(() => {
        Animated.timing(controlsFadeAnim, {
          toValue: 0,
          duration: 300,
          useNativeDriver: true,
        }).start(() => setIsControlsVisible(false));
      }, CONTROLS_TIMEOUT);
    }
  };

  const showControls = () => {
    setIsControlsVisible(true);
    Animated.timing(controlsFadeAnim, {
      toValue: 1,
      duration: 200,
      useNativeDriver: true,
    }).start();
    hideControlsWithDelay();
  };

  const handlePlayPause = async () => {
    if (!videoRef.current) return;
    
    animatePlayButton();
    
    if (isPlaying) {
      await videoRef.current.pauseAsync();
      showControls();
    } else {
      await videoRef.current.playAsync();
      hideControlsWithDelay();
    }
    setIsPlaying(!isPlaying);
  };

  const handleProgress = (status: AVPlaybackStatus) => {
    if (!status.isLoaded || isSeeking) return;

    const progress = status.positionMillis / (status.durationMillis || 1);
    setPosition(status.positionMillis || 0);
    progressWidth.setValue(progress);
  };

  const handleLoad = (status: AVPlaybackStatus) => {
    if (!status.isLoaded) return;

    setDuration(status.durationMillis || 0);
    setIsLoading(false);

    //      
    if (status.isLoaded && status.videoWidth && status.videoHeight) {
      setVideoWidth(status.videoWidth);
      setVideoHeight(status.videoHeight);
    }
  };

  const handleVideoPress = () => {
    if (!isControlsVisible) {
      showControls();
    }
  };

  const formatTime = (millis: number) => {
    if (!millis || isNaN(millis)) return '0:00';
    
    const totalSeconds = Math.floor(millis / 1000);
    const minutes = Math.floor(totalSeconds / 60);
    const seconds = totalSeconds % 60;
    return `${minutes}:${seconds < 10 ? '0' : ''}${seconds}`;
  };

  //      
  const videoStyle = {
    width: SCREEN_WIDTH,
    height: videoWidth && videoHeight 
      ? (SCREEN_WIDTH * videoHeight) / videoWidth 
      : SCREEN_WIDTH * 9/16,
    backgroundColor: '#000' as const,
  };

  return (
    <View style={[styles.container, style]}>
      <Pressable
        style={[styles.videoWrapper, videoStyle]}
        onPress={handleVideoPress}
      >
        <Video
          ref={videoRef}
          source={{ uri }}
          style={StyleSheet.absoluteFill}
          resizeMode={ResizeMode.CONTAIN}
          onPlaybackStatusUpdate={handleProgress}
          onLoad={handleLoad}
          isLooping
          useNativeControls={false}
        />

        {isLoading && (
          <View style={styles.loadingContainer}>
            <ActivityIndicator size="large" color="#fff" />
          </View>
        )}

        {isControlsVisible && (
          <Animated.View 
            style={[
              styles.controlsContainer,
              { opacity: controlsFadeAnim }
            ]}
          >
            <Animated.View 
              style={[
                styles.playButton,
                { transform: [{ scale: playButtonScale }] }
              ]}
            >
              <TouchableOpacity
                onPress={handlePlayPause}
                style={styles.playButtonTouchable}
              >
                <MaterialIcons
                  name={isPlaying ? 'pause' : 'play-arrow'}
                  size={50}
                  color="#fff"
                />
              </TouchableOpacity>
            </Animated.View>

            <View style={styles.bottomControls}>
              <View style={styles.progressContainer} {...panResponder.panHandlers}>
                <View style={styles.progressBar}>
                  <Animated.View 
                    style={[
                      styles.progress,
                      {
                        width: progressWidth.interpolate({
                          inputRange: [0, 1],
                          outputRange: ['0%', '100%']
                        })
                      }
                    ]} 
                  />
                  <Animated.View
                    style={[
                      styles.sliderThumb,
                      {
                        transform: [{
                          translateX: sliderPosition
                        }]
                      }
                    ]}
                  />
                </View>
                <View style={styles.timeContainer}>
                  <Text style={styles.timeText}>
                    {formatTime(position)}
                  </Text>
                  <Text style={styles.timeText}>
                    {formatTime(duration)}
                  </Text>
                </View>
              </View>
            </View>
          </Animated.View>
        )}
      </Pressable>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    backgroundColor: '#000',
    overflow: 'hidden',
    borderRadius: 12,
  },
  videoWrapper: {
    backgroundColor: '#000',
    overflow: 'hidden',
  },
  loadingContainer: {
    ...StyleSheet.absoluteFillObject,
    justifyContent: 'center',
    alignItems: 'center',
    backgroundColor: 'rgba(0, 0, 0, 0.3)',
  },
  controlsContainer: {
    ...StyleSheet.absoluteFillObject,
    justifyContent: 'space-between',
    padding: 16,
    backgroundColor: 'rgba(0, 0, 0, 0.4)',
  },
  playButton: {
    alignSelf: 'center',
    flex: 1,
    justifyContent: 'center',
  },
  playButtonTouchable: {
    width: 80,
    height: 80,
    borderRadius: 40,
    backgroundColor: 'rgba(0, 0, 0, 0.6)',
    justifyContent: 'center',
    alignItems: 'center',
  },
  bottomControls: {
    width: '100%',
  },
  progressContainer: {
    width: '100%',
    paddingHorizontal: 0,
  },
  progressBar: {
    height: 4,
    backgroundColor: 'rgba(255, 255, 255, 0.3)',
    borderRadius: 2,
    overflow: 'visible',
    position: 'relative',
  },
  progress: {
    height: '100%',
    backgroundColor: '#007AFF',
    borderRadius: 2,
  },
  sliderThumb: {
    width: 16,
    height: 16,
    borderRadius: 8,
    backgroundColor: '#fff',
    position: 'absolute',
    top: -6,
    marginLeft: -8,
    ...Platform.select({
      ios: {
        shadowColor: '#000',
        shadowOffset: { width: 0, height: 2 },
        shadowOpacity: 0.25,
        shadowRadius: 4,
      },
      android: {
        elevation: 5,
      },
    }),
  },
  timeContainer: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    marginTop: 8,
  },
  timeText: {
    color: '#fff',
    fontSize: 13,
    fontWeight: '600',
    textShadowColor: 'rgba(0, 0, 0, 0.5)',
    textShadowOffset: { width: 1, height: 1 },
    textShadowRadius: 2,
  },
});

export default CustomVideoPlayer; 